var me;
export default {
	data() {
		return {
			input:"",
			tuishuList:[],
			temp: [],
			InputBottom: 0,
			id:""
		}
	},
	onLoad(data) {
	   uni.showLoading({
	   	mask: true,
	   	title: "请稍后..."
	   });	
		me = this;
		me.id = data.id;
		me.refresh({success(title) {
			uni.setNavigationBarTitle({
				title:title
			})
		}});
	},
	onPullDownRefresh() {
		this.refresh()
	},
	methods: {
		refresh(a){
			uni.request({
				url: me.serverUrl+"/tuishu/getTuiShuAndPingLunByTuiShuId?tuishuId=" + me.id,
				method: "GET",
				success(res) {
					me.temp = [];
					var temp = res.data.data;
					me.temp = temp;
					console.log(temp);
					a.success(temp.title)
				},
				complete: () => {
					uni.hideNavigationBarLoading();
					uni.hideLoading();
					uni.stopPullDownRefresh();
				}
			})
		},
		addTuishu(){
			
			uni.request({
				url: me.serverUrl+"/tuishu/addPinglun" ,
				data:{
					tuishuId: me.temp.id,
					userId: me.temp.userId,
					content: me.input
					
				},
				method: "POST",
				header: {
					'content-type': 'application/x-www-form-urlencoded', 
				},
				success(res) {
					me.refresh();
					console.log("success")
				}
			})
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		}
	}
}
